import request from '../axios/request'


/**ruleIdruleId */
export function getRuleFieldsByRuleId(ruleId) {
    return request({
        url: `/rule_field/${ruleId}`,
        method: 'GET',
    })
}

/**idstartSequenceruleFieldValueautoIncrementSequenceStartSequence */
export function updateRuleFieldValueAutoIncrementSequenceStartSequenceById(params) {
    return request({
        url: `/rule_field/${params.id}/${params.startSequence}`,
        method: 'PUT',
    })
}

























/** */
export function addProduct(data) {
    return request({
        url: '/product',
        method: 'POST',
        data: data
    })
}

/** */
export function getProducts(params) {
    return request({
        url: `/product/${params.currentPage}/${params.pageSize}`,
        method: 'GET',
    })
}

/**ID */
export function deleteProductById(params) {
    return request({
        url: `/product/${params}`,
        method: 'DELETE',
    })
}

/** */
export function editProduct(data) {
    return request({
        url: '/product',
        method: 'PUT',
        data: data
    })
}

